@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;1,400;1,500&display=swap);

.input-item{
	margin:0.2vmax;
	width: 100%;
}
.buttons{

	width: 100%;
	display:grid;
	grid-area: buttons;
	grid-template-columns: 1fr 1fr;
	grid-template-areas:
	"submmit clear";
	align-items: center;
	justify-items: left;
}
.submmit{
	grid-area:submmit;
	cursor:pointer;
	-webkit-transform: scale(1.5);
	        transform: scale(1.5);
}
.clear{
	grid-area:clear;
	cursor:pointer;
	text-align: center;
	border:0.15vmax solid rgb(53, 153, 219);
	border-radius:0.5vmax;
	background-color:white;
}
.input-file{
	width:90%;
}
.input-container{
	display:grid;
	grid-template-rows:1fr 1fr;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-areas:
	"input input buttons"
	"png svg ngc";

	grid-gap: 1.5vmax;
	align-items:center;
	justify-items: left;

		
	padding:1vw;
	border:3px solid rgb(39, 183, 240);
	border-radius: 1.5vmax;
	background-color:rgb(201, 226, 243);
}
.text-container{
	grid-area:input;
}
.png-container{
	grid-area:png;
}
.svg-container{
	grid-area:svg;
}
.ngc-container{
	grid-area:ngc;
}


.item-wrapper{
	display:grid;
	grid-template-columns: 3fr repeat(6,1fr);
	grid-template-areas:
	"item png svg ngc info edit check";

	grid-gap:1vw ;
	padding:0 1vmax;
	
	align-items:center;
	border:0.15vmax solid rgb(30, 127, 165);
	border-radius: 0.5vmax;
	overflow:hidden;
	height:100%;
}
.list-item{
	grid-area: item;
	overflow: auto;
}
.btn{
	cursor:pointer;
	text-align: center;
	border:0.15vmax solid rgb(53, 153, 219);
	border-radius:0.5vmax;
	background-color:white;
}
.btn:hover{
	background-color: rgb(199, 188, 188);
}
.png{
	grid-area: png;
	text-align: center;
}
.svg{
	grid-area: svg;
	text-align: center;
}
.ngc{
	grid-area: ngc;
	text-align: center;
}
.info{
	grid-area: info;
}
.edit{
	grid-area: edit;

}
.check{
	grid-area: check;

}
.list-container{
	display:grid;
	width:70vw;
	margin-top:1vmax;
	grid-template-columns: 1fr;
	grid-template-rows: auto minmax(50vmax,auto);
	grid-gap:1.5vmax;
	justify-content:center;
}

.items-container{
	display:grid;
	grid-auto-flow: row;
	grid-auto-rows:3vmax;
	grid-gap:1.5vmax;
	align-items:center;

	padding: 1vw;
	min-height: 50vmax;

}
.items-not-checked{
	border:3px solid rgb(39, 240, 139);
	border-radius: 1.5vmax;
	background-color:rgb(201, 243, 218);
}
.items-checked{
	border:3px solid rgb(240, 180, 39);
	border-radius: 1.5vmax;
	background-color:rgb(240, 234, 181);
}
.switch-checked{
	justify-self:end;
	width:11vw;
	cursor:pointer;
	text-align: center;
	border:0.15vmax solid rgb(53, 153, 219);
	border-radius:0.5vmax;
	background-color:white;
}

.sidebar{
	position:fixed;
	display:grid;
	grid-template-rows: 15vw 4fr 1fr;
	grid-template-columns:9fr 1fr;
	grid-template-areas: 
	"img close"
	"info info"
	"files files";
	grid-row-gap:1vmax;
	margin-top:1vmax;
	left:72.5vw;
	top:0;
	width:23vw;
	height: 30vmax;
	padding:1vw;
	border: 0.2vw solid rgba(18, 95, 218, 0.424);
	background-color:rgba(114, 205, 233, 0.363);
	border-radius: 1vmax;
}
.info-container{
	display:grid;
	grid-area:info;
	grid-template-rows:1fr 1fr 1fr;
	grid-template-columns:1fr 4fr;
	grid-template-areas:
	"desc descText"
	"client clientText"
	"address addressText";
	grid-gap:1vmax;
	align-items: center;
}
.files-container{
	display:grid;
	grid-area:files;
	grid-template-columns:1fr 1fr 1fr;
	grid-template-areas:
	"png svg ngc";
	align-items: center;
}
.png-container{
	grid-area:png;
	text-align: center;
}
.svg-container{
	grid-area:svg;
	text-align: center;
}
.ngc-container{
	grid-area:ngc;
	text-align: center;
}

.description{
	grid-area:desc;
}
.description-text{
	grid-area:descText;
}
.client{
	grid-area:client;
}
.client-text{
	grid-area:clientText;
}
.address{
	grid-area:address;
}
.description-text{
	grid-area:addressText;
}
.close-icon{
	grid-area:close;
	justify-self:right;
	cursor:pointer;
	-webkit-transform:scale(1.5);
	        transform:scale(1.5);
}

.text{
	padding: 0.5vmax;
	height:1.1vmax;
	background-color:white;
	overflow-x:auto;
	overflow-y:hidden;
}
.text-wrapper{
	border: 0.2vw solid rgba(18, 95, 218, 0.424);
	border-radius:1vmax;
	overflow:hidden;
}
.image{
	justify-self:center;
	align-self:center;
	max-width:100%;
	max-height:100%;
	border-radius:1vmax;
}

.main-container{
	display:grid;
	margin-top:1vmax;
	justify-items: center;
}

.auth-container{
	display:grid;
	grid-template-columns:1fr 1fr;
	grid-template-areas:
	"login register";
	grid-gap:1vmax;
	width:70vw;
	min-height:30vmax;
}

.login{
	grid-area:login;
}
.register{
	grid-area:register;
}

.container{
	display:grid;
	grid-template-rows: repeat(2,1fr);
	grid-template-areas:
	"title"
	"form";
	margin:1vmax;
	justify-items: center;
	align-items: center;
	box-shadow: 0px 0px 15px 0px rgba(199,199,199,1);
	border-radius: 1.5vmax;
	height: 40vmax;
}
.title-container{
	min-width:15vw;
	text-align:center;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	padding:0.4vmax;
	grid-area:title;
	border-radius:0.5vmax;
	background-color:white;
}

.form-container{
	height:100%;
	width:100%;
	grid-area:form;
	display:grid;
	grid-template-rows:1fr 1fr 1fr;
	grid-template-areas:
	"username"
	"password"
	"submit";
	justify-items: center;
	align-items: center;
}
.input{
	width:13vw;
}
.username-input{
	grid-area:username;
	width: 50%;
  border-radius: 15px;
	box-shadow: 0px 0px 5px 0px rgba(199,199,199,1);
	border: none;
	height:50px;

}
.username-input[type='text']{
	font-size: 15px;
	font-weight: 600;
	letter-spacing: 1px;
	font-family: Roboto;
	text-align: center;
	color: #282828;
}

.password-input{
	grid-area:password;
	width: 50%;
	border-radius: 15px;
	box-shadow: 0px 0px 5px 0px rgba(199,199,199,1);
	border: none;
	height:50px;

}
.password-input[type='password']{
	font-size: 15px;
	font-weight: 600;
	letter-spacing: 1px;
	font-family: Roboto;
	text-align: center;
	color: #282828;
}

.password-input::-webkit-input-placeholder{
	font-size: 15px;
	font-weight: 600;
	letter-spacing: 1px;
	font-family: Roboto;
	text-align: center;
}

.password-input:-ms-input-placeholder{
	font-size: 15px;
	font-weight: 600;
	letter-spacing: 1px;
	font-family: Roboto;
	text-align: center;
}

.password-input::placeholder{
	font-size: 15px;
	font-weight: 600;
	letter-spacing: 1px;
	font-family: Roboto;
	text-align: center;
}
.username-input::-webkit-input-placeholder{
	font-size: 15px;
	font-weight: 600;
	letter-spacing: 1px;
	font-family: Roboto;
	text-align: center;
}
.username-input:-ms-input-placeholder{
	font-size: 15px;
	font-weight: 600;
	letter-spacing: 1px;
	font-family: Roboto;
	text-align: center;
}
.username-input::placeholder{
	font-size: 15px;
	font-weight: 600;
	letter-spacing: 1px;
	font-family: Roboto;
	text-align: center;
}
.submit{
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	grid-area:submit;
	padding:0.3vmax;
	min-width:5vw;
	cursor:pointer;
	text-align: center;
	border:0.15vmax solid rgb(53, 153, 219);
	border-radius:1vmax;
	background-color:white;
}
.title-text{
	font-size: 3.5vw;
	font-weight: 600;
	letter-spacing: 1px;
	font-family: Roboto;

}

